/* src/components/Home.css */
body {
    font-family: Arial, sans-serif;
    background-color: #e6b507;
    color: black;
    margin: 5px;
    margin-left: 5%;
    margin-right: 5%;
    padding: 0;
    display: flex;
    flex-direction: column;
    min-height: 100vh; /* Ensure the body takes at least the full viewport height */
  }
  
  header {
    background-color: blue;
    padding: 10px;
    text-align: center;
    border-style: solid;
    border-color: yellow;
  }
  
  header h1 {
    font-size: 20px;
  }
  
  nav {
    text-align: center;
    padding: 10px;
  }
  
  nav a {
    color: black;
    text-decoration: none;
    margin: 0 10px;
  }
  
  nav a:hover {
    text-decoration: underline;
  }
  
  main {
    flex: 1; /* Fill remaining space in the viewport */
    padding: 20px;
  }
  
  footer {
    background-color: blue;
    padding: 10px;
    text-align: center;
  }
  
  img {
    height: 50%;
    width: 50%;
  }

  .latest-info pre{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-left: -5%;
    font-size: 80%;
    
  }
  